import { BaseValueObject } from '../base';

export class ProjectName extends BaseValueObject<'ProjectName', string> {
  static from(value: string): ProjectName {
    return new ProjectName(value);
  }
  protected validate(value: string): string {
    const trimmed = value.trim();

    if (trimmed.length === 0) {
      throw new Error('ProjectName must not be empty');
    }

    if (trimmed.length > 256) {
      throw new Error('ProjectName has maximum length of 256');
    }

    return value;
  }
}

